import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
//components
import { PerformanceMetrices, ProfileCard, ProfileImage } from "..";
import Spinner from "../../../views/progress/Spinner";
//services
import { getUser } from "../../../api/userService";
import "../../../css/profile.css";
import useWindowSize from "../../Hook/useWindowSize";

const ReviewDetails = () => {
  const screenSize = useWindowSize();
  const { colleagueId } = useParams();
  const [colleagueInfo, setColleagueInfo] = useState({});
  const [showSpinner, setShowSpinner] = useState(false);
  async function fetchApi() {
    setShowSpinner(true);
    try {
      const response = await getUser(colleagueId);

      setColleagueInfo(response?.data);
    } catch (error) {
      console.log("error ", error);
    }
    setShowSpinner(false);
  }
  useEffect(() => {
    fetchApi();
  }, []);
  if (showSpinner) return <Spinner />;
  return (
    <>
      <div id="profileHeight" className="profile ">
        <div style={{ width: "70%" }}>
          <ProfileCard page="Review" user={colleagueInfo} />
        </div>
        <div style={{ width: "30%" }} className="profile-img">
          <ProfileImage
            user={colleagueInfo}
            page="Review"
            width="6rem"
            height="6rem"
          />
        </div>
      </div>
      <div
        id="scrollHeight"
        className="scrollSection "
        style={{ height: screenSize, paddingTop: "10px" }}
      >
        <PerformanceMetrices colleagueId={colleagueInfo?.id} />
      </div>
    </>
  );
};

export default ReviewDetails;
