import { configureStore } from "@reduxjs/toolkit";
import contentSliceReducer from "../features/content/contentSlice";
import errorModalSliceReducer from "../features/errors/errorModalSlice";
import permissionSliceReducer from "../features/loggedin/permission";
import userSliceReducer from "../features/loggedin/userSlice";
import performanceSliceReducer from "../features/performance/performanceSlice";
import storage from "redux-persist/lib/storage";
import { combineReducers } from "redux";
import { persistReducer } from "redux-persist";
import teamsReducer from "../features/admin/team";

import statusReducer from "../features/status/statusSlice";
const reducers = combineReducers({
  content: contentSliceReducer,
  performance: performanceSliceReducer,
  errorModal: errorModalSliceReducer,
  loggedin: userSliceReducer,
  permission: permissionSliceReducer,
  teams: teamsReducer,
  status: statusReducer,
});

const persistConfig = {
  key: "root",
  blacklist: ["status"],
  storage,
};

const persistedReducer = persistReducer(persistConfig, reducers);

export const store = configureStore({
  reducer: persistedReducer,
  middleware: (getDefaultMiddleware) => getDefaultMiddleware(),
});
