import http from "./httpService";
//EP-> end point
const resetPasswordEP = "api/auth/user/reset-password";
//const updatePhoneNumberEP = "/api/user/phone";

export async function resetPassword(obj) {
  try {
    const { data } = await http.post(resetPasswordEP, obj);
    return data;
  } catch (error) {
    console.log(error);
  }
}

const authObj = { resetPassword };

export default authObj;
