import { Toolbar } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useLocation } from "react-router";
import routes from "../utils/routes";
import ViewTeamMembers from "../views/pages/team/ViewTeamMembers";
import ColleagueProfile from "./content/colleague/ColleagueProfile";
import ReviewDetails from "./content/colleague/ReviewDetails";

const drawerWidth = 240;

const AppContent = () => {
  const location = useLocation();

  const path = location.pathname;
  let component = routes.filter((route) => route.path === path);
  if (component.length <= 0 && path.includes("/team/members")) {
    component = [{ render: <ViewTeamMembers /> }];
  } else if (component.length <= 0 && path.includes("/colleagues/review")) {
    component = [{ render: <ReviewDetails /> }];
  } else if (component.length <= 0 && path.includes("/colleagues/profile")) {
    component = [{ render: <ColleagueProfile /> }];
  } else if (component.length <= 0 && path.includes("/team/member/profile")) {
    component = [{ render: <ColleagueProfile /> }];
  }
  return (
    <Box
      component="main"
      sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
    >
      <Toolbar />
      {component[0].render}
    </Box>
  );
};

export default AppContent;
