import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import { BlueButton } from "../../buttons";
import ErrorMessages from "../../forms/ErrorMessages";
import {
  CustomEmailField,
  CustomPasswordField,
} from "../../forms/fields/Input";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { loginHandler } from "../../../api/login";
import FormHeading from "../../forms/FormHeading";
import { useDispatch } from "react-redux";
import { setErrorModal } from "../../../features/errors/errorModalSlice";
import { Link } from "react-router-dom";
import { getUserProfile } from "../../../api/profileService";
import { setUser } from "../../../features/loggedin/userSlice";
import CustomSnackbar from "../../forms/snackbars/CustomSnackbar";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [status, setStatus] = useState(-1);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const handleSubmit = (e) => {
    e.preventDefault();

    loginHandler({ email, password })
      .then((res) => {
        if (res?.data) {
          localStorage.setItem("token", res?.data);
        } else {
          setInfo(res?.messages[0]);
          setInfoType("error");
          setShowAlert(true);
        }
        setStatus(res?.statusCode);
      })
      .catch((err) => {
        console.log("err login ", err.message);
      });
  };

  const storeUser = async () => {
    try {
      const { data } = await getUserProfile();

      console.log("user ", data);
      dispatch(setUser(data));
      if (status === 0) {
        navigate("/dashboard");
      } else if (status === 200) {
        navigate("/set-new-password");
      }
    } catch (error) {}
  };

  useEffect(() => {
    if (status === 0 || status === 200) {
      storeUser();
    }
  }, [status]);

  return (
    <>
      <div className="form-container">
        <Box
          className="form"
          onSubmit={handleSubmit}
          component="form"
          autoComplete="off"
          sx={{ width: "20rem" }}
        >
          <FormHeading text="Login to your account" />
          <Card
            className="login-card"
            sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
          >
            <div className="card-item">
              <CustomEmailField email={email} setEmail={setEmail} />
            </div>

            <div className="card-item">
              <CustomPasswordField
                password={password}
                setPassword={setPassword}
              />
            </div>

            <div className="card-item" style={{ marginBottom: "10px" }}>
              <BlueButton text="Sign In" />
            </div>

            <div
              style={{
                textAlign: "right",
                display: "block",
                width: "95%",
              }}
            >
              <Link
                to="/forgot-password"
                className="forgot-password"
                onClick={() => console.log("clicked")}
              >
                Forgot Password?
              </Link>
            </div>
          </Card>
        </Box>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default Login;
