import { useState } from "react";

//components
import { InputBox } from "../forms/fields/Input";
import DialogBox from "./dialogBox";
import CustomSnackbar from "../forms/snackbars/CustomSnackbar";
import ImageUploadButton from "./../buttons/ImageUploadButton";

//service
import { teamSchema } from "./../../utils/validation";
import { createTeam } from "../../api/adminService";
import "../../css/team.css";

export default function CreateTeamDialog({ open, handleClose }) {
  const [teamName, setTeamName] = useState("");
  const [teamDetails, setTeamDetails] = useState("");
  const [teamLeadEmail, setTeamLeadEmail] = useState("");
  const [image, setImage] = useState(null);

  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState();
  const [showAlert, setShowAlert] = useState(false);

  const handleChange = (file) => setImage(file);
  const closeAlert = () => setShowAlert(false);
  const handleSubmit = async () => {
    try {
      await teamSchema.validate(
        { teamLeadEmail, teamName, teamDetails, image },
        { abortEarly: false }
      );
      let formData = new FormData();
      formData.append("Email", teamLeadEmail);
      formData.append("Name", teamName);
      formData.append("Description", teamDetails);
      formData.append("Image", image);
      const response = await createTeam(formData);
      response?.statusCode === 201
        ? setInfoType("success")
        : setInfoType("error");
      setInfo(response?.messages[0]);
      handleClose();
    } catch (error) {
      setInfo(error?.message);
      setInfoType("error");
      console.log("error ", error);
    }
    setShowAlert(true);
  };
  return (
    <>
      <DialogBox
        open={open}
        handleClose={handleClose}
        title={"Create Team"}
        btnName={"Submit"}
        btnColor={"#159EDA"}
        handleConfirmation={handleSubmit}
        variant={"contained"}
      >
        <div className="form">
          <label>Team Lead Email</label>
          <InputBox
            type={"text"}
            value={teamLeadEmail}
            handleChange={(e) => setTeamLeadEmail(e.target.value)}
            size={"small"}
          />
          <label>Team Name</label>
          <InputBox
            type={"text"}
            value={teamName}
            handleChange={(e) => setTeamName(e.target.value)}
            size={"small"}
          />

          <label>Team Details</label>
          <InputBox
            type={"text"}
            value={teamDetails}
            handleChange={(e) => setTeamDetails(e.target.value)}
            size={"small"}
          />
          <label>Team logo</label>
          <ImageUploadButton text="Upload" onChange={handleChange} />
        </div>
      </DialogBox>

      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
}
