// import React from "react";
// import ReactDOM from "react-dom";
import Select from "react-select";

function Dropdown() {
  const customStyles = {
    control: (base, state) => ({
      ...base,
      background: "#159eda",
      width: "10rem",
      boxShadow: state.isFocused ? null : null,
      "&:hover": {
        borderColor: state.isFocused ? "#159eda" : "#159eda",
      },
    }),
    menu: (provided, state) => ({
      ...provided,
      /* width: "100px", */
      borderBottom: "5px dotted #159eda",

      padding: 20,
      width: "10rem",
    }),
    dropdownIndicator: (provided, state) => ({
      ...provided,
      color: "white",
      "&:hover": {
        color: "#ffffff",
      },
    }),

    placeholder: (provided, state) => ({
      ...provided,
      color: "#ffffff",
      "&:hover": {
        color: "#ffffff",
      },
    }),
    indicatorSeparator: (provided, state) => ({
      ...provided,

      backgroundColor: "white",
    }),
    singleValue: (provided) => ({
      ...provided,
      color: "white",
    }),
    option: (styles, { data, isDisabled, isFocused, isSelected }) => {
      return {
        ...styles,
        backgroundColor: isDisabled
          ? undefined
          : isSelected
          ? "#159eda"
          : isFocused
          ? "#d2d2d2"
          : undefined,
        color: isDisabled ? "#ccc" : isSelected ? "white" : "black",
        cursor: isDisabled ? "not-allowed" : "default",

        ":active": {
          ...styles[":active"],
          backgroundColor: !isDisabled
            ? isSelected
              ? "#159eda"
              : "#159eda"
            : undefined,
        },
      };
    },
  };

  const options = [
    {
      label: "option 1",
      value: 1,
    },
    {
      label: "option 2",
      value: 2,
    },
    {
      label: "option 3",
      value: 3,
    },
    {
      label: "option 4",
      value: 4,
    },
    {
      label: "option 5",
      value: 5,
    },
  ];

  return (
    <div className="App">
      <Select styles={customStyles} options={options} />
    </div>
  );
}

export default Dropdown;
