import React from "react";
import FormTemplate from "../../forms/FormTemplate";

const ChangePassword = () => {
  const fields = {
    confirmPassword: true,
    newPassword: true,
  };

  return (
    <>
      <FormTemplate
        fields={fields}
        heading="Set New Password"
        btn="Set Password"
      />
    </>
  );
};

export default ChangePassword;
