import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

//components
import { InputBox } from "../forms/fields/Input";
import DialogBox from "./dialogBox";
import CustomSnackbar from "../forms/snackbars/CustomSnackbar";
import ImageUploadButton from "../buttons/ImageUploadButton";
import "../../css/team.css";

//service
import { teamSchema } from "../../utils/validation";
import { updateTeam } from "../../api/adminService";
import { currentTeamUpdated } from "../../features/admin/team";
import { getUser } from "../../api/userService";

export default function UpdateTeamDialog({ open, handleClose }) {
  const dispatch = useDispatch();
  const team = useSelector((state) => state.teams.currentActiveTeam);
  const [teamName, setTeamName] = useState();
  const [teamDetails, setTeamDetails] = useState();
  const [image, setImage] = useState();
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState();
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const [teamLeadEmail, setTeamLeadEmail] = useState("");

  const handleChange = (file) => setImage(file);
  const getTeamLeadDetails = async (userId) => {
    try {
      const response = await getUser(userId);
      setTeamLeadEmail(response?.data?.email);
    } catch (error) {
      console.log("error ", error);
    }
  };

  const handleSubmit = async () => {
    try {
      await teamSchema.validate(
        { teamLeadEmail, teamName, teamDetails, image },
        { abortEarly: false }
      );
      console.log("email ", teamLeadEmail);
      let formData = new FormData();
      formData.append("Email", teamLeadEmail);
      formData.append("Name", teamName);
      formData.append("Description", teamDetails);
      formData.append("Image", image);
      const response = await updateTeam(formData, team?.id);
      if (response?.statusCode === 201) {
        setInfoType("success");
        dispatch(currentTeamUpdated(response?.data));
      } else setInfoType("error");

      setInfo(response?.messages?.length && response?.messages[0]);
      handleClose();
    } catch (error) {
      setInfoType("error");
      setInfo(error.message);
    }
    setShowAlert(true);
  };
  useEffect(() => {
    console.log("team update", team);
    setTeamName(team?.name);
    setTeamDetails(team?.description || "");
    getTeamLeadDetails(team?.userId);
  }, [team]);
  return (
    <>
      <DialogBox
        open={open}
        handleClose={handleClose}
        title={"Update Team"}
        btnName={"Update"}
        handleConfirmation={handleSubmit}
        variant={"contained"}
      >
        <div className="form">
          {/*    <label>Team Lead Email</label>
          <InputBox
            type={"text"}
            value={teamLeadEmail}
            handleChange={(e) => setTeamLeadEmail(e.target.value)}
            size={"small"}
          /> */}
          <label>Team Name</label>
          <InputBox
            type={"text"}
            value={teamName}
            handleChange={(e) => setTeamName(e.target.value)}
            size={"small"}
          />

          <label>Team Details</label>
          <InputBox
            type={"text"}
            value={teamDetails}
            handleChange={(e) => setTeamDetails(e.target.value)}
            size={"small"}
          />
          <label>Team logo</label>
          <ImageUploadButton text="Upload" onChange={handleChange} />
        </div>
      </DialogBox>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
}
