import { Button } from "@mui/material";
import React from "react";

const SkyBlueButton = ({
  text,
  handleClick,
  padding = "5px 20px",
  ...rest
}) => {
  return (
    <Button
      {...rest}
      variant="contained"
      sx={{
        fontFamily: "Barlow",
        backgroundColor: "#159eda",
        color: "white",
        padding: { padding },
        textTransform: "capitalize",
      }}
      onClick={handleClick}
    >
      {text}
    </Button>
  );
};

export default SkyBlueButton;
