import { useState } from "react";
//components
import { InputBox } from "../forms/fields/Input";
import { Box } from "@mui/system";
import DialogBox from "./dialogBox";
import { BlueButton } from "../buttons";

//service
import { addTeamMember } from "../../api/adminService";
import CustomSnackbar from "./../forms/snackbars/CustomSnackbar";

export default function AddTeamMemberDialog({ open, handleClose, teamId }) {
  const [userEmail, setUserEmail] = useState();
  const [alertMessage, setAlertMessage] = useState();
  const [showAlert, setShowAlert] = useState(false);
  const [alertType, setAlertType] = useState("");
  const closeAlert = () => setShowAlert(false);

  const handleChange = (e) => setUserEmail(e.target.value);
  const handleSubmit = async (e) => {
    try {
      e.preventDefault();
      const { statusCode, messages } = await addTeamMember(userEmail, teamId);
      if (statusCode !== 200) {
        setAlertType("error");
        setAlertMessage(messages[0]);
      } else {
        setAlertType("success");
        setAlertMessage("Team member added successfully");
        setUserEmail("");
        handleClose();
      }
      setShowAlert(true);
    } catch (error) {
      console.log("error ", error);
    }
  };

  return (
    <>
      <DialogBox
        open={open}
        handleClose={handleClose}
        contentText={"Enter Official Email"}
        isActionVisible={false}
      >
        <Box
          className="form"
          onSubmit={handleSubmit}
          component="form"
          autoComplete="off"
          sx={{ width: "20rem" }}
        >
          <InputBox
            type="email"
            value={userEmail}
            handleChange={handleChange}
            size={"small"}
            style={{ marginBottom: "10px" }}
          />

          <BlueButton text="Submit" />
        </Box>
      </DialogBox>
      <CustomSnackbar
        text={alertMessage}
        type={alertType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
}
