import http from "./httpService";
//EP-> end point
const getUserEP = "api/user/";
const getColleaguesEp = "/api/team/colleagues";
export async function getUser(id) {
  try {
    const { data } = await http.get(getUserEP + id);
    return data;
  } catch (error) {
    throw error;
  }
}

export async function getColleagues() {
  try {
    const { data } = await http.get(getColleaguesEp);
    return data;
  } catch (error) {
    throw error;
  }
}

const colleagueObj = { getUser, getColleagues };

export default colleagueObj;
