import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
//components
import FormHeading from "../../forms/FormHeading";
import CustomSnackbar from "../../forms/snackbars/CustomSnackbar";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { BlueButton } from "../../buttons";
import {
  CustomConfirmPasswordField,
  CustomEmailField,
  CustomNewPasswordField,
} from "../../forms/fields/Input";
import "../../../css/login.css";
//services
import { firstTimePasswordSetupHandler } from "../../../api/first-time-password-setup";

const FirstTimePasswordSetup = () => {
  const [email, setEmail] = useState("");
  const [newPassword, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const [status, setStatus] = useState(-1);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();

    firstTimePasswordSetupHandler({
      email,
      newpassword: newPassword,
      confirmpassword: confirmPassword,
    }).then((res) => {
      if (res?.statusCode === 200) {
        const { statusCode } = res;
        setStatus(statusCode);
      } else {
        setInfo(res?.messages[0]);
        setInfoType("error");
        setShowAlert(true);
      }
    });
  };

  useEffect(() => {
    if (status === 200) {
      navigate("/dashboard");
    }
    //eslint-disable-next-line
  }, [status]);

  return (
    <>
      <div className="form-container">
        <div className="form">
          <Box
            onSubmit={handleSubmit}
            component="form"
            autoComplete="off"
            sx={{ width: "100%" }}
          >
            <FormHeading text="Set Your New Password" />

            <Card
              className="login-card"
              sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
            >
              <div className="card-item">
                <CustomEmailField email={email} setEmail={setEmail} />
              </div>

              <div className="card-item">
                <CustomNewPasswordField
                  newpassword={newPassword}
                  setNewPassword={setPassword}
                />
              </div>

              <div className="card-item">
                <CustomConfirmPasswordField
                  confirmpassword={confirmPassword}
                  setConfirmPassword={setConfirmPassword}
                />
              </div>
              <div className="card-item" style={{ marginBottom: "10px" }}>
                <BlueButton text="SET PASSWORD" />
              </div>
            </Card>
          </Box>
        </div>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default FirstTimePasswordSetup;
