import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { BlueButton } from "../../buttons";
import { CustomEmailField, CustomTextField } from "../../forms/fields/Input";
import { registerHandler } from "../../../api/registration";

import FormHeading from "../../forms/FormHeading";
import "../../../css/login.css";
import withAlert from "./../../../components/HOC/withAlert";
import { setStatus } from "../../../features/status/statusSlice";

const Registration = () => {
  const dispatch = useDispatch();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const handleSubmit = (e) => {
    e.preventDefault();

    registerHandler({ userName: name, email })
      .then((res) => {
        let message;
        if (res?.statusCode === 201) {
          message = "User created Successfully";
          setName("");
          setEmail("");
        } else message = res?.messages[0];
        dispatch(setStatus({ statusCode: res?.statusCode, message }));
      })
      .catch((err) => {
        dispatch(
          setStatus({ statusCode: err?.status, message: err.statusText })
        );
      });
  };

  return (
    <>
      <div className="form-container">
        <div className="form">
          <Box
            onSubmit={handleSubmit}
            component="form"
            autoComplete="off"
            sx={{ width: "100%" }}
          >
            <FormHeading text="Create New User" />
            <Card
              className="login-card"
              sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
            >
              <div className="card-item">
                <CustomTextField name={name} setName={setName} />
              </div>

              <div className="card-item">
                <CustomEmailField email={email} setEmail={setEmail} />
              </div>

              <div className="card-item" style={{ marginBottom: "10px" }}>
                <BlueButton text="Sign Up" />
              </div>
            </Card>
          </Box>
        </div>
      </div>
    </>
  );
};

export default withAlert(Registration, 201);
