import React from "react";
import Dropdown from "../../../views/dropdowns/Dropdown";
import PerformanceMetrices from "./PerformanceMetrices";

const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const MonthlyPerformance = () => {
  return (
    <>
      <div className="dropdown">
        <Dropdown items={months} label="Month" />
        <Dropdown items={[2021, 2020, 1019]} label="Year" />
      </div>

      <div>
        <PerformanceMetrices />
      </div>
    </>
  );
};

export default MonthlyPerformance;
