import React, { useState } from "react";
import { CustomEmailField } from "../../forms/fields/Input";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import FormHeading from "../../forms/FormHeading";
import { BlueButton } from "../../buttons";
import { forgotPasswordHandler } from "../../../api/forgotPassword";
import SuccessSnackbar from "../../forms/snackbars/Success";
import CustomSnackbar from "../../forms/snackbars/CustomSnackbar";

const ForgotPassword = () => {
  const [email, setEmail] = useState("");
  const [msg, setMsg] = useState("");
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const handleSubmit = (e) => {
    e.preventDefault();

    forgotPasswordHandler(email)
      .then((res) => {
        console.log(res);
        setEmail("");
        //setMsg(res);
        setInfo(res);
        setInfoType("success");
      })
      .catch((err) => {
        setInfo("Invalid Email!");
        setInfoType("error");
      });
    setShowAlert(true);
  };

  return (
    <>
      <div className="form-container">
        <div className="form">
          <Box
            onSubmit={handleSubmit}
            component="form"
            autoComplete="off"
            sx={{ width: "100%" }}
          >
            <FormHeading text="Enter Your Email" />
            <Card
              className="login-card"
              sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
            >
              <div className="card-item">
                <CustomEmailField email={email} setEmail={setEmail} />
              </div>

              {/* {msg && <SuccessSnackbar text={msg} />} */}

              <div className="card-item" style={{ marginBottom: "10px" }}>
                <BlueButton text="Submit" />
              </div>
            </Card>
          </Box>
        </div>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default ForgotPassword;
