import React, { useState } from "react";
import { AppBar } from "@mui/material";
import { useSelector } from "react-redux";
import "../css/appHeader.css";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";
import { useNavigate } from "react-router";
import DialogBox from "./../views/dialog/dialogBox";

const drawerWidth = 300;

const AppHeader = ({ handleDrawerToggle }) => {
  const navigate = useNavigate();
  const state = useSelector((state) => state.loggedin.user);
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const onClickLogout = () => setOpen(true);
  const handleCloseDialog = () => setOpen(false);
  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleLogout = () => {
    localStorage.removeItem("token");
    handleClose();
    navigate("/login");
  };

  const onClickProfile = () => {
    handleClose();
    navigate("/me");
  };
  const handleClose = () => setAnchorEl(null);
  return (
    <>
      <AppBar
        id="headerHeight"
        position="fixed"
        sx={{
          ml: { sm: `${drawerWidth}px` },
          backgroundColor: "#1171A8",
          boxShadow: 0,
        }}
      >
        <Toolbar>
          <IconButton
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            sx={{
              display: "flex",
              justifyContent: "flex-end",
              width: "100%",
              textTransform: "capitalize",
            }}
            variant="h6"
            noWrap
            component="div"
            onClick={handleMenu}
          >
            {state.username}
          </Typography>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <MenuItem onClick={onClickProfile}>Profile</MenuItem>
            <MenuItem onClick={onClickLogout}>Logout</MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>
      <DialogBox
        open={open}
        handleClose={handleCloseDialog}
        title={"Logout"}
        btnName={"logout"}
        contentText={"Are you sure you want to logout?"}
        handleConfirmation={handleLogout}
      />
    </>
  );
};

export default AppHeader;
