import React from "react";
import Dropdown from "../../../views/dropdowns/Dropdown";
import PerformanceMetrices from "./PerformanceMetrices";

const AnuualPerformance = () => {
  return (
    <div>
      <div className="dropdown">
        <Dropdown items={[2021, 2020, 1019]} label="Year" />
      </div>
      <div>
        <PerformanceMetrices />
      </div>
    </div>
  );
};

export default AnuualPerformance;
