import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { useState } from "react";
import { useSearchParams, useNavigate } from "react-router-dom";
import { resetPassword } from "../../../api/authService";
import { BlueButton } from "../../buttons";
import { PasswordField } from "../../forms/fields/Input";
import FormHeading from "./../../forms/FormHeading";
import CustomSnackbar from "./../../forms/snackbars/CustomSnackbar";

const ResetPassword = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  // const [errorMessage, setErrorMessage] = useState();
  // const [showError, setShowError] = useState(false);
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, SetShowAlert] = useState(false);
  const closeAlert = () => SetShowAlert(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      // setErrorMessage(["Password doesn't match"]);
      // setShowError(true);
      setInfoType("error");
      setInfo("Password doesn't match");
      SetShowAlert(true);
      return;
    }
    const token = searchParams.get("Data")?.replace(/ /g, "+");
    const email = searchParams.get("email");
    try {
      const response = await resetPassword({
        token,
        email,
        password,
        confirmPassword,
      });
      // if (statusCode !== 200 && statusCode > 0) {
      //   setErrorMessage(messages);
      //   setShowError(true);
      // } else navigate("/login");

      if (response?.statusCode !== 200) {
        setInfoType("error");
        setInfo(response?.messages.join("\r\n"));
      } else navigate("/login");
    } catch (error) {
      setInfoType("error");
      setInfo(error?.message);
    }
    SetShowAlert(true);
  };

  return (
    <>
      <div className="form-container">
        <div className="form">
          <Box
            onSubmit={handleSubmit}
            component="form"
            autoComplete="off"
            sx={{ width: "100%" }}
          >
            <FormHeading text="Set Your New Password" />

            <Card
              className="login-card"
              sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
            >
              <div className="card-item">
                <PasswordField
                  password={password}
                  setPassword={setPassword}
                  label="Type New Password"
                />
              </div>

              <div className="card-item">
                <PasswordField
                  password={confirmPassword}
                  setPassword={setConfirmPassword}
                  label="Confirm Password"
                />
              </div>

              {/* {showError && (
                <ErrorMessages
                  messages={errorMessage}
                  showError={setShowError}
                  label="Confirm New Password"
                />
              )} */}

              <div className="card-item" style={{ marginBottom: "10px" }}>
                <BlueButton text="SET PASSWORD" />
              </div>
            </Card>
          </Box>
        </div>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default ResetPassword;
