import { setUser } from "../features/loggedin/userSlice";
import { store } from "./../app/store";
const LOCAL_STORAGE_KEY = "user";

export const getItemFromLS = (item) => {
  const currentItem =
    localStorage.getItem(LOCAL_STORAGE_KEY) &&
    JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY))[item];
  return currentItem;
};

export const updateItemToLS = (itemName, value) => {
  const currentObj = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
  currentObj[itemName] = value;
  localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(currentObj));
  store.dispatch(setUser(currentObj));
};

export function capitalizeFirstLetter(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export function getScrollHeight() {
  const headerHeight =
    document.getElementById("headerHeight")?.offsetHeight || 0;
  const profileHeight =
    document.getElementById("profileHeight")?.offsetHeight || 0;
  const screenHeight =
    /* document.getElementById("root")?.offsetHeight */ window.innerHeight || 0;
  const padding = 50;
  const remainingHeight =
    screenHeight - (headerHeight + profileHeight + padding);
  if (document.getElementById("scrollHeight")) {
    document.getElementById("scrollHeight").style.height =
      remainingHeight + "px";
  }

  return remainingHeight;
}

const obj = {
  getItemFromLS,
  updateItemToLS,
  capitalizeFirstLetter,
  getScrollHeight,
};
export default obj;
