import http from "./httpService";
const teamEP = "api/team";

export async function createTeam(formData) {
  try {
    const { data } = await http.post(teamEP, formData);
    return data;
  } catch (error) {
    error.message = "Something went wrong";
    throw error;
  }
}

export async function getTeams() {
  try {
    const { data } = await http.get(teamEP);
    return data;
  } catch (error) {
    console.log(error);
    throw error;
  }
}
export async function getSingleTeam(teamId) {
  try {
    const { data } = await http.get(teamEP + "/" + teamId);
    return data;
  } catch (error) {
    throw error;
  }
}

export async function addTeamMember(email, teamId) {
  try {
    const { data } = await http.post(`${teamEP}/${email}/member/${teamId}`);
    return data;
  } catch (error) {
    console.log(error);
  }
}

export async function RemoveTeamMember(teamId, userId) {
  try {
    const { data } = await http.delete(`${teamEP}/${teamId}/member/${userId}`);
    return data;
  } catch (error) {
    throw error;
  }
}

export async function updateTeam(formData, teamId) {
  try {
    const { data } = await http.put(teamEP, formData, { params: { teamId } });
    return data;
  } catch (error) {
    console.log(error);
    throw error;
  }
}

export async function deleteTeam(id) {
  try {
    const { data } = await http.delete(`${teamEP}/${id}`);
    return data;
  } catch (error) {
    console.log(error);
    throw error;
  }
}

const adminObj = {
  createTeam,
  getTeams,
  getSingleTeam,
  RemoveTeamMember,
  updateTeam,
  deleteTeam,
};

export default adminObj;
