import { Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { SkyBlueButton } from "../../views/buttons";
import Team from "../../views/pages/team/team";
import CreateTeamDialog from "../../views/dialog/createTeamDialog";
import { getTeams } from "../../api/adminService";
import Spinner from "../../views/progress/Spinner";

const Teams = () => {
  const [data, dataSet] = useState([]);
  const [open, setOpen] = useState(false);
  const [isTeamDeleted, setIsTeamDeleted] = useState(false);
  const [showSpinner, setShowSpinner] = useState(false);
  const handleClose = () => setOpen(false);
  const handleClick = () => setOpen(true);
  async function getAllTeams() {
    setShowSpinner(true);
    try {
      let response = await getTeams();
      dataSet(response?.data);
    } catch (error) {
      console.log("error ", error);
    }
    setShowSpinner(false);
  }
  useEffect(() => {
    getAllTeams();
  }, [open, isTeamDeleted]);
  if (showSpinner) return <Spinner />;
  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <div style={{ margin: "auto" }}>
          <SkyBlueButton text={"Create New Team"} handleClick={handleClick} />
        </div>
        <Typography
          style={{ margin: "15px", fontFamily: "Barlow" }}
          align="center"
          color={"#159eda"}
          variant="h6"
        >
          Teams
        </Typography>
        <hr />
      </div>
      <div className="container">
        {!data?.length && (
          <Typography
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            component={"div"}
            variant={"p"}
          >
            Currently no team created
          </Typography>
        )}
        {data?.map((team) => (
          <Team
            team={team}
            isTeamDeleted={isTeamDeleted}
            setIsTeamDeleted={setIsTeamDeleted}
          />
        ))}
      </div>
      <CreateTeamDialog open={open} handleClose={handleClose} />
    </>
  );
};

export default Teams;
