import { Card } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import {
  TEXT,
  EMAIL,
  PASSWORD,
  CONFIRMPASSWORD,
  NEWPASSWORD,
} from "../../utils/constants";
import { BlueButton } from "../buttons";
import Input from "./fields/Input";
import "../../css/login.css";
import { Link } from "react-router-dom";

const FormTemplate = ({ fields, heading, btn }) => {
  return (
    <div className="form-container">
      <div className="form">
        <Box component="form" autoComplete="off" sx={{ width: "100%" }}>
          <label className="form-heading">{heading}</label>
          <Card className="login-card">
            {fields.text && (
              <div className="card-item">
                <Input {...TEXT} width="100%" />
              </div>
            )}

            {fields.email && (
              <div className="card-item">
                <Input {...EMAIL} width="100%" />
              </div>
            )}

            {fields.password && (
              <div className="card-item">
                <Input {...PASSWORD} width="100%" />
              </div>
            )}

            {fields.newPassword && (
              <div className="card-item">
                <Input {...NEWPASSWORD} width="100%" />
              </div>
            )}

            {fields.confirmPassword && (
              <div className="card-item">
                <Input {...CONFIRMPASSWORD} width="100%" />
              </div>
            )}

            <div className="card-item" style={{ marginBottom: "10px" }}>
              <BlueButton text={btn} width="100%" />
            </div>
            {btn === "Login" && (
              <div
                style={{
                  textAlign: "right",
                  display: "block",
                  width: "95%",
                }}
              >
                <Link to="/password_reset" className="forgot-password">
                  Forgot Password?
                </Link>
              </div>
            )}
          </Card>
        </Box>
      </div>
    </div>
  );
};

export default FormTemplate;
