import React, { useState } from "react";
//components
import { Card, Typography, Grid } from "@mui/material";
import { SkyBlueButton } from "../../../views/buttons/";
import UpdateNumberDialog from "../../../views/dialog/updateNumberDialog";

const ProfileCard = ({ page, user }) => {
  const [open, setOpen] = useState(false);
  const handleClose = () => setOpen(false);
  const handleEdit = () => setOpen(true);

  return (
    <Card sx={{ padding: "10px" }}>
      <Grid minHeight={"10rem"} container>
        <Grid item xs={6}>
          {user !== "undefined" &&
            Object.keys(user)?.map((item) => {
              if (
                item === "id" ||
                item === "image" ||
                item === "token" ||
                item === "role"
              )
                return null;
              return (
                <Typography
                  sx={{ textTransform: "capitalize" }}
                  align="left"
                  gutterBottom
                >
                  {item}:{" "}
                </Typography>
              );
            })}
        </Grid>
        <Grid item xs={6}>
          {user !== "undefined" &&
            Object.keys(user)?.map((item) => {
              if (
                item === "id" ||
                item === "image" ||
                item === "token" ||
                item === "role"
              )
                return null;
              return (
                <Typography
                  align="left"
                  sx={{ marginLeft: "10px" }}
                  gutterBottom
                >
                  {user[item]}{" "}
                  {item === "phone" && page === "Profile" && (
                    <SkyBlueButton text="Edit" handleClick={handleEdit} />
                  )}
                </Typography>
              );
            })}
        </Grid>
      </Grid>
      <UpdateNumberDialog open={open} handleClose={handleClose} />
    </Card>
  );
};

export default ProfileCard;
