import http from "./httpService";

const uploadProfileImageEP = "api/user/profilepicture";
const updatePhoneNumberEP = "api/user/phone";
const getUserProfileEP = "api/User/profile";

export async function uploadProfileImage(file) {
  try {
    const { data } = await http.post(uploadProfileImageEP, file);
    return data;
  } catch (error) {
    console.log(error);
  }
}

export async function updatePhoneNumber(phone) {
  try {
    const { data } = await http.put(updatePhoneNumberEP, { phone });
    return data;
  } catch (error) {
    console.log(error);
  }
}

export async function getUserProfile() {
  try {
    const { data } = await http.get(getUserProfileEP);
    return data;
  } catch (error) {
    console.log(error);
  }
}

const profileObj = { uploadProfileImage, updatePhoneNumber, getUserProfileEP };

export default profileObj;
