import { createSlice } from "@reduxjs/toolkit";

const slice = createSlice({
  name: "teams",
  initialState: {
    allTeams: {},
    currentActiveTeam: {},
  },
  reducers: {
    teamsAdded: (teams, action) => {
      teams.allTeams = { ...action.payload };
    },
    currentTeamAdded: (teams, action) => {
      teams.currentActiveTeam = { ...action.payload };
    },
    currentTeamUpdated: (teams, action) => {
      for (const [key, value] of Object.entries(action.payload)) {
        teams.currentActiveTeam[key] = value;
      }
    },
  },
});

export const { teamsAdded, currentTeamAdded, currentTeamUpdated } =
  slice.actions;

export default slice.reducer;
