import axios from "axios";

export const firstTimePasswordSetupHandler = async ({
  email,
  newpassword,
  confirmpassword,
}) => {
  const promise = axios.post(
    "http://api.pra.studio-23.xyz/api/Auth/user/setup/new-password",
    {
      email,
      newpassword,
      confirmpassword,
    }
  );

  const data = promise.then((res) => res.data);
  return data;
};
