import { useEffect, useState } from "react";

const useWindowSize = () => {
  const [screenSize, setScreenSize] = useState([]);

  //   function getScrollHeight() {
  //     return remainingHeight;
  //   }
  useEffect(() => {
    // const checkScreenSize = () => {
    //     setOnSmallScreen(window.innerWidth < screenSize);
    // };

    // checkScreenSize();
    // window.addEventListener('resize', checkScreenSize);
    const headerHeight =
      document.getElementById("headerHeight")?.offsetHeight || 0;
    const profileHeight =
      document.getElementById("profileHeight")?.offsetHeight || 0;
    const screenHeight = window.innerHeight || 0;
    const padding = 60;
    const remainingHeight =
      screenHeight - (headerHeight + profileHeight + padding);
    document.getElementById("scrollHeight").style.height =
      remainingHeight + "px";
    setScreenSize(remainingHeight);
    //window.addEventListener("scroll");
  }, []);

  return screenSize;
};

export default useWindowSize;
