import { useState } from "react";
//components
import { InputBox } from "../forms/fields/Input";
import SuccessSnackbar from "../forms/snackbars/Success";
import DialogBox from "./dialogBox";

//service
import parsePhoneNumber from "libphonenumber-js";
import { updatePhoneNumber } from "../../api/profileService";
import { useDispatch } from "react-redux";
import { userUpdated } from "../../features/loggedin/userSlice";

export default function UpdateNumberDialog({ open, handleClose }) {
  const dispatch = useDispatch();
  const [error, setError] = useState(false);
  const [phoneNumber, setPhoneNumber] = useState("");
  const [helperText, setHelperText] = useState("");
  const [status, setStatus] = useState(false);

  const handleUpdateNumber = async () => {
    const isPhoneNumberValid = parsePhoneNumber(phoneNumber, "BD");

    if (!isPhoneNumberValid || !isPhoneNumberValid.isValid()) {
      setError(true);
      setHelperText("Invalid Number!");
      return;
    }
    try {
      const { data, statusCode } = await updatePhoneNumber(phoneNumber);
      const { phone } = data;
      dispatch(userUpdated({ phone }));
      handleClose();
      setStatus(statusCode);
    } catch (error) {
      console.log("error ", error.message);
    }
  };
  const handleChange = (e) => {
    setError(false);
    setHelperText("");
    setPhoneNumber(e.target.value);
  };
  return (
    <>
      <DialogBox
        open={open}
        handleClose={handleClose}
        title={"Update Phone Number"}
        btnName={"Update"}
        handleConfirmation={handleUpdateNumber}
      >
        <InputBox
          type={"number"}
          value={phoneNumber}
          handleChange={handleChange}
          helperText={helperText}
          label={"Phone number"}
          error={error}
          variant={"filled"}
        />
      </DialogBox>
      {status === 201 && <SuccessSnackbar text="Number Updated Successfully" />}
    </>
  );
}
