import React, { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router";
//components
import { ProfileAvatar } from "../../../views/avatars";
import { SkyBlueButton } from "../../../views/buttons";
import AddIcon from "@mui/icons-material/Add";
import { Badge, Typography } from "@mui/material";
import RemoveCircleSharpIcon from "@mui/icons-material/RemoveCircleSharp";
import AddTeamMemberDialog from "../../dialog/addTeamMemberDialog";
import DialogBox from "../../dialog/dialogBox";
import CustomSnackbar from "./../../forms/snackbars/CustomSnackbar";
import UpdateTeamDialog from "../../dialog/updateTeamDialog";
//store
import { useDispatch, useSelector } from "react-redux";
import { currentTeamAdded } from "../../../features/admin/team";
//service
import { getSingleTeam, RemoveTeamMember } from "../../../api/adminService";
import { apiUrl } from "../../../config.json";
import Spinner from "../../progress/Spinner";

const ViewTeamMembers = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const team = useSelector((state) => state.teams.currentActiveTeam);
  const { teamId } = useParams();
  const [userId, setUserId] = useState();
  //dialog states
  const [open, setOpen] = useState(false);
  const [openUpdateTeamDialog, setOpenUpdateTeamDialog] = useState(false);
  const [openRemoveDialog, setOpenRemoveDialog] = useState(false);

  //alert states
  const [showSpinner, setShowSpinner] = useState(false);
  const [alertMessage, setAlertMessage] = useState();
  const [alertType, setAlertType] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);

  //dialog open & close functions
  const handleCloseRemoveDialog = () => setOpenRemoveDialog(false);
  const handleCloseUpdateDialog = () => setOpenUpdateTeamDialog(false);
  const handleClose = () => setOpen(false);
  const onClickAdd = () => setOpen(true);
  const onClickEdit = () => setOpenUpdateTeamDialog(true);
  const onClickRemove = (userId) => {
    setUserId(userId);
    setOpenRemoveDialog(true);
  };
  const onClickProfile = (userId) => {
    navigate(`/team/member/profile/${userId}`);
  };
  const fetchApi = useCallback(
    async (teamId) => {
      setShowSpinner(true);
      try {
        const response = await getSingleTeam(teamId);
        console.log("response vm ", response?.data);
        dispatch(currentTeamAdded(response?.data));
      } catch (error) {
        setAlertType("error");
        setAlertMessage(error.message);
        setShowAlert(true);
      }
      setShowSpinner(false);
    },
    [dispatch]
  );
  async function handleRemoveMember() {
    try {
      const response = await RemoveTeamMember(teamId, userId);
      if (response?.statusCode === 200) {
        setAlertType("success");
        dispatch(currentTeamAdded(response?.data));
        handleCloseRemoveDialog();
      } else setAlertType("error");

      setAlertMessage(response?.messages[0]);
      setUserId("");
    } catch (error) {
      setAlertType("error");
      setAlertMessage(error.message);
    }
    setShowAlert(true);
  }

  useEffect(() => {
    fetchApi(teamId);
  }, [teamId, open, fetchApi]);
  if (showSpinner) return <Spinner />;
  return (
    <>
      <div>
        <label
          style={{
            display: "block",
            textAlign: "center",
            color: "#159EDA",
            fontSize: "20px",
            marginBottom: "30px",
          }}
        >
          {team?.name}
        </label>
        <div
          style={{
            textAlign: "end",
            margin: "10px",
          }}
        >
          <SkyBlueButton
            text="Edit Team"
            handleClick={onClickEdit}
            style={{ marginRight: "10px" }}
          />
          <SkyBlueButton
            text="Add Member"
            handleClick={onClickAdd}
            endIcon={<AddIcon />}
          />
        </div>
        {!team?.teamMembers?.length ? (
          <Typography
            sx={{
              display: "flex",
              justifyContent: "center",
            }}
            component={"div"}
            variant={"p"}
          >
            Currently no members in this team
          </Typography>
        ) : (
          <div className="container-view-team-members">
            {team?.teamMembers?.map((member) => (
              <div>
                <label
                  style={{
                    display: "block",
                    width: "100%",
                    textAlign: "center",
                    color: "#159EDA",
                    fontSize: "18px",
                    marginBottom: "10px",
                  }}
                >
                  {member?.username}
                </label>
                <div
                  style={{
                    marginBottom: "10px",
                    textAlign: "center",
                  }}
                >
                  <Badge
                    onClick={() => onClickRemove(member?.userId)}
                    badgeContent={
                      <RemoveCircleSharpIcon
                        style={{
                          color: "#737373",
                          fontSize: "28px",
                          cursor: "pointer",
                        }}
                      />
                    }
                    color="primary"
                    anchorOrigin={{ vertical: "top", horizontal: "right" }}
                    sx={{ backgroundColor: "none" }}
                  >
                    <ProfileAvatar
                      imageSrc={apiUrl + member?.image}
                      width="150px"
                      height="150px"
                    />
                  </Badge>
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <SkyBlueButton
                    handleClick={() => onClickProfile(member?.userId)}
                    text="Profile"
                  />
                </div>
              </div>
            ))}
          </div>
        )}
        <AddTeamMemberDialog
          open={open}
          handleClose={handleClose}
          teamId={teamId}
        />
        <UpdateTeamDialog
          open={openUpdateTeamDialog}
          handleClose={handleCloseUpdateDialog}
          //team={team}
        />
        <DialogBox
          open={openRemoveDialog}
          handleClose={handleCloseRemoveDialog}
          contentText={"Are you sure you want to remove this member?"}
          btnName={"Remove"}
          variant={"contained"}
          btnColor={"error"}
          handleConfirmation={handleRemoveMember}
        />
      </div>
      <CustomSnackbar
        text={alertMessage}
        type={alertType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default ViewTeamMembers;
