const Input = ({ label, type, width }) => {
  return (
    <FormControl
      variant="outlined"
      color="primary"
      style={{ display: "block", width: `${width}` }}
    >
      <CssTextField fullWidth label={label} type={type} variant="outlined" />
    </FormControl>
  );
};

export default Input;

export const InputBox = ({
  type,
  handleChange,
  value,
  helperText,
  label,
  error,
  variant,
  size,
  ...rest
}) => {
  return (
    <TextField
      required
      fullWidth
      type={type}
      value={value}
      onChange={handleChange}
      helperText={helperText}
      label={label}
      error={error}
      variant={variant}
      size={size}
      {...rest}
    />
  );
};

export const PasswordField = ({ password, setPassword, label }) => {
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <CssTextField
      fullWidth
      // {...NEWPASSWORD}
      label={label}
      type={showPassword ? "text" : "password"}
      required
      value={password}
      onChange={(e) => setPassword(e.target.value)}
      InputProps={{
        endAdornment: (
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              edge="end"
            >
              {showPassword ? <VisibilityOff /> : <Visibility />}
            </IconButton>
          </InputAdornment>
        ),
      }}
    />
  );
};
