import { Tab, Tabs } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useDispatch } from "react-redux";
import { setTab } from "../../features/performance/performanceSlice";
import { tabs } from "../../utils/_performanceTabs";

const PerformanceTabsList = () => {
  const [value, setValue] = React.useState(0);
  const dispatch = useDispatch();

  const handleChange = (event, newValue) => {
    dispatch(setTab(newValue));
    setValue(newValue);
  };

  return (
    <Box sx={{ borderColor: "divider" }}>
      <Tabs
        TabIndicatorProps={{
          style: {
            backgroundColor: "#159EDA",
            border: "2px solid #159EDA",
          },
        }}
        style={{ color: "red" }}
        variant="fullWidth"
        value={value}
        onChange={handleChange}
        aria-label="basic tabs example"
      >
        {tabs.map((item, index) => {
          return (
            <Tab
              sx={{
                textTransform: "capitalize",
                fontSize: "15px",
                fontWeight: "bold",
                color: "#159EDA!important",
              }}
              key={index}
              label={item.name}
            />
          );
        })}
      </Tabs>
    </Box>
  );
};

export default PerformanceTabsList;
