import http from "./httpService";
//EP-> end point
const ratingCriteriaEP = "/api/rating/criteira";
const postRatingEP = "/api/rating";
const skipRatingEP = "/api/rating/skip-rating";
export async function getRatingCriteria() {
  try {
    const { data } = await http.get(ratingCriteriaEP);
    return data;
  } catch (error) {
    throw error;
  }
}
export async function postRatings(obj) {
  try {
    const { data } = await http.post(postRatingEP, obj);
    return data;
  } catch (error) {
    throw error;
  }
}

export async function skipReview(userId) {
  try {
    const { data } = await http.post(skipRatingEP, { userId });
    return data;
  } catch (error) {
    throw error;
  }
}
const colleagueObj = { getRatingCriteria, postRatings, skipReview };

export default colleagueObj;
