import { PerformanceTabsList } from "../../../views/tabs";
import { useSelector } from "react-redux";
import { tabs } from "../../../utils/_performanceTabs";
import "../../../css/performance.css";

const Performance = () => {
  const state = useSelector((state) => state.performance.selectedTab);

  return (
    <div className="container">
      <PerformanceTabsList />
      <div>{tabs[state].component}</div>
    </div>
  );
};

export default Performance;
