import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import CustomSnackbar from "../../views/forms/snackbars/CustomSnackbar";
import { useDispatch } from "react-redux";
import { setStatus } from "../../features/status/statusSlice";
export const withAlert = (WrappedComponent, successStatusCode) => {
  return () => {
    const dispatch = useDispatch();
    const { statusCode, message } = useSelector((state) => state.status.info);
    const [info, setInfo] = useState("");
    const [infoType, setInfoType] = useState();
    const [showAlert, setShowAlert] = useState(false);
    const closeAlert = () => setShowAlert(false);

    useEffect(() => {
      if (!statusCode || statusCode === 0) return;
      else if (statusCode === successStatusCode) {
        setInfoType("success");
        setInfo(message);
      } else {
        setInfoType("error");
        setInfo(message);
      }
      setShowAlert(true);
      dispatch(setStatus({ statusCode: 0, message: "" }));
      //eslint-disable-next-line
    }, [statusCode, message]);
    return (
      <>
        <WrappedComponent />
        <CustomSnackbar
          text={info}
          type={infoType}
          open={showAlert}
          handleClose={closeAlert}
        />
      </>
    );
  };
};
export default withAlert;
