import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
import { BlueButton } from "../../buttons";
import ErrorMessages from "../../forms/ErrorMessages";
import {
  CustomEmailField,
  CustomPasswordField,
} from "../../forms/fields/Input";
import { Card } from "@mui/material";
import { Box } from "@mui/system";
import { loginHandler } from "../../../api/login";
import FormHeading from "../../forms/FormHeading";
import CustomSnackbar from "../../forms/snackbars/CustomSnackbar";

const FirstTimeLogin = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [status, setStatus] = useState(-1);
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    loginHandler({ email, password })
      .then((res) => {
        setStatus(res?.statusCode);
        res?.statusCode !== 200 ? setInfoType("error") : setInfoType("success");
        setInfo(res?.messages[0]);
      })
      .catch((err) => {
        setInfoType("error");
        setInfo(err?.message);
      });
    setShowAlert(true);
  };

  useEffect(() => {
    if (status === 200) {
      navigate("/set-new-password");
    }
    //eslint-disable-next-line
  }, [status]);
  return (
    <>
      <div className="form-container">
        <Box
          onSubmit={handleSubmit}
          component="form"
          className="form"
          autoComplete="off"
          sx={{ width: "20rem" }}
        >
          <FormHeading text="Login to your account" />
          <Card
            className="login-card"
            sx={{ boxShadow: "5px 5px 5px 5px #C6C6C6" }}
          >
            <div className="card-item">
              <CustomEmailField email={email} setEmail={setEmail} />
            </div>

            <div className="card-item">
              <CustomPasswordField
                password={password}
                setPassword={setPassword}
              />
            </div>

            <div className="card-item" style={{ marginBottom: "10px" }}>
              <BlueButton text="Sign In" />
            </div>
          </Card>
        </Box>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default FirstTimeLogin;
