import React, { useEffect, useState } from "react";
//components
import { Rating, Typography } from "@mui/material";
import DialogBox from "../../../views/dialog/dialogBox";
import CustomSnackbar from "../../../views/forms/snackbars/CustomSnackbar";
import SkyBlueButton from "./../../../views/buttons/SkyBlueButton";
//services
import { getRatingCriteria, postRatings } from "../../../api/ratingService";

const PerformanceMetrices = ({ colleagueId }) => {
  const [ratingList, setRatingList] = useState([]);
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [open, setOpen] = useState(false);
  const [showAlert, SetShowAlert] = useState(false);
  const closeAlert = () => SetShowAlert(false);
  const handleCloseDialog = () => setOpen(false);
  const onClickSubmitReview = () => setOpen(true);
  async function fetchApi() {
    try {
      const response = await getRatingCriteria();
      const newArray = response?.data?.map((rating) => ({
        ...rating,
        value: 0,
      }));
      setRatingList(newArray);
    } catch (error) {
      setInfoType("error");
      setInfo(error?.message);
      SetShowAlert(true);
    }
  }
  const handleSubmitReview = async () => {
    try {
      const response = await postRatings({
        userId: colleagueId,
        ratingCriterias: ratingList,
      });
      if (response?.statusCode === 200) setInfoType("success");
      else setInfoType("error");
      setInfo(response?.messages[0]);
    } catch (error) {
      setInfoType("error");
      setInfo(error?.message);
    }
    handleCloseDialog();
    SetShowAlert(true);
  };
  const handleRatingChange = (value, index) => {
    const copiedArray = [...ratingList];
    copiedArray[index].value = value;
    setRatingList(copiedArray);
  };

  useEffect(() => {
    fetchApi();
  }, []);
  return (
    <>
      <div className="ratingContainer">
        {ratingList?.map((rating, index) => (
          <div>
            <Typography fontSize={"26px"} component="legend">
              {rating?.name}
            </Typography>
            <Rating
              max={10}
              name="simple-controlled"
              sx={{ fontSize: "3rem" }}
              value={ratingList[index]?.value}
              onChange={(event, newValue) => {
                handleRatingChange(newValue, index);
              }}
            />
          </div>
        ))}
        <div className="ratingBtn">
          <SkyBlueButton
            handleClick={onClickSubmitReview}
            text={"Submit Review"}
            padding={"5px 40px"}
          />
        </div>
      </div>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
      <DialogBox
        open={open}
        handleClose={handleCloseDialog}
        btnName={"Confirm"}
        variant={"contained"}
        handleConfirmation={handleSubmitReview}
      >
        <div style={{ textAlign: "center" }}>
          <p>
            Confirm Submission? <br />
            You wont be able to change it.
            <br />
            Cancel if you want to change something.
          </p>
        </div>
      </DialogBox>
    </>
  );
};

export default PerformanceMetrices;
