import React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import _sidebar from "../utils/_sidebar";
import { SidebarItem } from "./";
import "../css/appSidebar.css";
import { setContent } from "../features/content/contentSlice";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useLocation } from "react-router";
import ListItem from "@mui/material/ListItem";
import Divider from "@mui/material/Divider";
import { AppBar } from "@mui/material";

const drawerWidth = 300;

const AppSidebar = ({ container, handleDrawerToggle, mobileOpen }) => {
  const dispatch = useDispatch();
  const location = useLocation();

  const role = useSelector((state) => state.loggedin.user?.role);

  const path = location.pathname;
  let ind = _sidebar.findIndex(
    (route) => route.to.toString() === path.toString()
  );

  if (ind < 0) ind = 0;

  const [selectedIndex, setSelectedIndex] = React.useState(ind);
  const handleClick = (e, index) => {
    setSelectedIndex(index);
  };

  const drawer = (
    <div>
      <Toolbar sx={{ backgroundColor: "#1171A8" }}>
        <AppBar
          position="fixed"
          sx={{
            ml: { sm: `${drawerWidth}px` },
            backgroundColor: "#1171A8",
          }}
        ></AppBar>
      </Toolbar>
      <Divider />
      <List>
        {_sidebar?.map((item, index) => {
          //rendering teams tab depends on role
          if (!role?.includes("Admin") && item.name === "Teams") return null;
          return (
            <Link
              to={item.to}
              style={{ textDecoration: "none" }}
              key={item.name}
            >
              <ListItem
                button
                key={index}
                sx={{
                  [`&:hover`]: {
                    backgroundColor: "#E3EEFA",
                    boxShadow: 0,
                  },
                  color: "#159eda",
                  boxShadow: "3px 3px #efefef",
                  margin: "13px 10px",
                  width: "90%",
                  cursor: "pointer",
                }}
                selected={selectedIndex === index}
                onClick={(e) => {
                  handleClick(e, index);
                  dispatch(setContent(item.name));
                }}
              >
                <SidebarItem item={item} />
              </ListItem>
            </Link>
          );
        })}
      </List>
    </div>
  );

  return (
    <Box
      component="nav"
      sx={{
        width: { sm: drawerWidth },
        flexShrink: { sm: 0 },
        // zIndex: -1,
      }}
      aria-label="mailbox folders"
    >
      <Drawer
        container={container}
        variant="temporary"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true,
        }}
        sx={{
          display: { xs: "block", sm: "none" },
          "& .MuiDrawer-paper": { boxSizing: "border-box", width: drawerWidth },
        }}
      >
        {drawer}
      </Drawer>
      <Drawer
        variant="permanent"
        sx={{
          display: { xs: "none", sm: "block" },
          "& .MuiDrawer-paper": { boxSizing: "border-box", width: drawerWidth },
        }}
        open
      >
        {drawer}
      </Drawer>
    </Box>
    // <Drawer
    //   variant="permanent"
    //   sx={{
    //     width: drawerWidth,
    //     flexShrink: 0,
    //     [`& .MuiDrawer-paper`]: {
    //       width: drawerWidth,
    //       boxSizing: "border-box",
    //     },
    //     [`& .Mui-selected`]: {
    //       backgroundColor: "#c7e4f2",
    //       boxShadow: "0",
    //     },
    //   }}
    // >
    //   <AppBar
    //     position="fixed"
    //     sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
    //   >
    //     <AppHeader />
    //   </AppBar>
    //   <Toolbar />
    //   <Box
    //     sx={{
    //       overflow: "auto",
    //     }}
    //   >
    //     <List>
    //       {_sidebar.map((item, index) => {
    //         return (
    //           <Link
    //             to={item.to}
    //             style={{ textDecoration: "none" }}
    //             key={item.name}
    //           >
    //             <ListItem
    //               button
    //               key={index}
    //               sx={{
    //                 [`&:hover`]: {
    //                   backgroundColor: "#E3EEFA",
    //                   boxShadow: 0,
    //                 },
    //                 color: "#159eda",
    //                 boxShadow: "3px 3px #efefef",
    //                 margin: "13px 10px",
    //                 width: "90%",
    //                 cursor: "pointer",
    //               }}
    //               selected={selectedIndex === index}
    //               onClick={(e) => {
    //                 handleClick(e, index);
    //                 dispatch(setContent(item.name));
    //               }}
    //             >
    //               <SidebarItem item={item} />
    //             </ListItem>
    //           </Link>
    //         );
    //       })}
    //     </List>
    //   </Box>
    // </Drawer>
  );
};

export default AppSidebar;
