import React, { useState } from "react";
import { useDispatch } from "react-redux";
//components
import { ProfileAvatar } from "../../../views/avatars/";
import { ImageUploadButton, SkyBlueButton } from "../../../views/buttons/";
import { Grid, Typography } from "@mui/material";
import DialogBox from "../../../views/dialog/dialogBox";
import CustomSnackbar from "../../../views/forms/snackbars/CustomSnackbar";

//services
import { uploadProfileImage } from "../../../api/profileService";
import { apiUrl } from "../../../config.json";
import { userUpdated } from "./../../../features/loggedin/userSlice";
import { skipReview } from "../../../api/ratingService";

const ProfileImage = ({ page, user, ...rest }) => {
  const dispatch = useDispatch();
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState("");
  const [showAlert, SetShowAlert] = useState(false);
  const [open, setOpen] = useState(false);
  const handleCloseDialog = () => setOpen(false);
  const closeAlert = () => SetShowAlert(false);

  const handleChange = async (file) => {
    try {
      let formData = new FormData();
      formData.append("Image", file);
      const { data } = await uploadProfileImage(formData);

      dispatch(userUpdated({ image: data }));
    } catch (error) {
      console.log("error ", error.message);
    }
  };
  const onClickSkipReview = () => setOpen(true);
  const handleSkipReview = async () => {
    try {
      const response = await skipReview(user?.id);
      response?.statusCode === 200
        ? setInfoType("success")
        : setInfoType("error");
      handleCloseDialog();
      setInfo(response?.messages[0]);
    } catch (error) {
      setInfoType("error");
      setInfo(error?.message);
    }
    SetShowAlert(true);
  };
  return (
    <>
      <Grid
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
      >
        <Grid item>
          <ProfileAvatar imageSrc={apiUrl + user?.image} {...rest} />
          <Typography
            align="center"
            variant="h6"
            sx={{ textTransform: "capitalize" }}
          >
            {user?.username}
          </Typography>
          <Typography component="div" align="center" variant="caption">
            {user?.designation}
          </Typography>
        </Grid>
        <Grid item style={{ marginTop: "10px" }}>
          {page === "Profile" ? (
            <ImageUploadButton onChange={handleChange} text="Change Image" />
          ) : page === "Review" ? (
            <SkyBlueButton handleClick={onClickSkipReview} text="Skip Review" />
          ) : (
            <></>
          )}
        </Grid>
      </Grid>
      <DialogBox
        open={open}
        handleClose={handleCloseDialog}
        btnName={"Yes"}
        variant={"contained"}
        btnColor={"warning"}
        handleConfirmation={handleSkipReview}
      >
        <div style={{ textAlign: "center" }}>
          <p>Do you want to skip the review?</p>

          <p> It means </p>
          <p>
            1.You dont work with the person or <br />
            2. You dont know him at all
          </p>
          <br />
        </div>
      </DialogBox>
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </>
  );
};

export default ProfileImage;
