import { Button } from "@mui/material";
import React from "react";

const BlueButton = ({ text }) => {
  return (
    <Button
      type="submit"
      fullWidth
      variant="contained"
      style={{
        backgroundColor: "#1171A8",
        color: "white",
        padding: "5px 15px",
        fontWeight: "bold",
        fontFamily: "Barlow",
      }}
    >
      {text}
    </Button>
  );
};

export default BlueButton;
