import { useState } from "react";
import { useNavigate } from "react-router";
//components
import { Box } from "@mui/system";
import { SkyBlueButton } from "../../buttons";
import { Badge, Card, Typography } from "@mui/material";
import logo from "../../../assets/images/bs.png";
import DialogBox from "../../dialog/dialogBox";
import CustomSnackbar from "./../../forms/snackbars/CustomSnackbar";
import RemoveCircleSharpIcon from "@mui/icons-material/RemoveCircleSharp";
//services
import { apiUrl } from "../../../config.json";
import { deleteTeam } from "../../../api/adminService";

const Team = ({ team, isTeamDeleted, setIsTeamDeleted }) => {
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const [info, setInfo] = useState("");
  const [infoType, setInfoType] = useState();
  const [showAlert, setShowAlert] = useState(false);
  const closeAlert = () => setShowAlert(false);
  const closeDialog = () => setOpen(false);
  const onClickRemove = () => setOpen(true);

  const addDefaultSrc = (ev) => (ev.target.src = logo);
  const handleViewTeam = (teamId) => navigate(`/team/members/${teamId}`);

  const handleTeamRemove = async () => {
    try {
      const { statusCode, messages } = await deleteTeam(team?.id);
      statusCode !== 200 ? setInfoType("error") : setInfoType("success");
      setInfo(messages?.length && messages[0]);
      setIsTeamDeleted(!isTeamDeleted);
    } catch (err) {
      setInfoType("error");
      setInfo(err.message);
    }
    setShowAlert(true);
    closeDialog();
  };
  return (
    <div style={{ width: "100%", textAlign: "center" }}>
      <Typography
        color={"#159eda"}
        component={"div"}
        variant="caption"
        align="center"
        sx={{
          marginBottom: "10px",
          marginTop: "10px",
          whiteSpace: "nowrap",
          overflow: "hidden",
          textOverflow: "ellipsis",
        }}
      >
        {team?.name}
      </Typography>
      <div style={{ display: "inline-block" }}>
        <Badge
          onClick={onClickRemove}
          badgeContent={
            <RemoveCircleSharpIcon
              style={{
                color: "#737373",
                fontSize: "25px",
                cursor: "pointer",
              }}
            />
          }
          color="primary"
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          sx={{ backgroundColor: "none" }}
        >
          <Card variant="outlined">
            <img
              className="center-cropped"
              src={apiUrl + team.image}
              alt="logo"
              onError={addDefaultSrc}
            />
          </Card>
        </Badge>
        <Box mt={1}>
          <SkyBlueButton
            text={"View Team"}
            handleClick={() => handleViewTeam(team.id)}
          />
        </Box>
      </div>
      <DialogBox
        open={open}
        handleClose={closeDialog}
        contentText={"Are you sure you want to remove this team?"}
        btnName={"Remove"}
        variant={"contained"}
        btnColor={"error"}
        handleConfirmation={handleTeamRemove}
      />
      <CustomSnackbar
        text={info}
        type={infoType}
        open={showAlert}
        handleClose={closeAlert}
      />
    </div>
  );
};

export default Team;
