import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router";
//components
import { ProfileAvatar } from "../../views/avatars";
import { SkyBlueButton } from "../../views/buttons";
import Spinner from "../../views/progress/Spinner";

import "../../css/profile.css";
//services
import { apiUrl } from "../../config.json";
import { getColleagues } from "../../api/userService";
const MyColleagues = () => {
  const [colleagues, setColleagues] = useState();
  const [showSpinner, setShowSpinner] = useState(false);
  const navigate = useNavigate();
  async function fetchApi() {
    setShowSpinner(true);
    try {
      const response = await getColleagues();
      console.log("colleague ", response);
      setColleagues(response?.data);
    } catch (error) {
      console.log("error ", error);
    }
    setShowSpinner(false);
  }
  const handleProfile = (userId) => navigate(`/colleagues/review/${userId}`);
  useEffect(() => {
    fetchApi();
  }, []);
  if (showSpinner) return <Spinner />;
  return (
    <>
      <div>
        <label
          style={{
            display: "block",
            textAlign: "center",
            color: "#159EDA",
            fontSize: "20px",
            marginBottom: "30px",
          }}
        >
          Colleagues
        </label>

        {!colleagues?.length ? (
          <p style={{ textAlign: "center" }}>No Colleague Found</p>
        ) : (
          <div className="container-view-team-members">
            {colleagues?.map((colleague) => (
              <div>
                <label
                  style={{
                    display: "block",
                    width: "100%",
                    textAlign: "center",
                    color: "#159EDA",
                    fontSize: "18px",
                    marginBottom: "10px",
                  }}
                >
                  {colleague?.username}
                </label>
                <div
                  style={{
                    marginBottom: "10px",
                    textAlign: "center",
                  }}
                >
                  <ProfileAvatar
                    imageSrc={apiUrl + colleague?.image}
                    width="8rem"
                    height="8rem"
                  />
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <SkyBlueButton
                    text="Profile"
                    handleClick={() => handleProfile(colleague?.id)}
                  />
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
      {/* <CustomSnackbar
        text={alertMessage}
        type={alertType}
        open={showAlert}
        handleClose={closeAlert}
      /> */}
    </>
  );
};

export default MyColleagues;
