import ReviewDetails from "../components/content/colleague/ReviewDetails";
import Registration from "../views/pages/registration/Registration";
import FirstTimePasswordSetup from "../views/pages/first-time-password/FirstTimePasswordSetup";
import ForgotPassword from "../views/pages/forgot-password/ForgotPassword";
import FirstTimeLogin from "../views/pages/first-time-login/FirstTimeLogin";
import ChangePassword from "../views/pages/change-password/ChangePassword";
import Login from "../views/pages/login/Login";
import ViewTeamMembers from "../views/pages/team/ViewTeamMembers";
import {
  ColleagueReview,
  Profile,
  Performance,
  Colleagues,
  Teams,
} from "../components/content";
import Layout from "../layout/Layout";
import ResetPassword from "../views/pages/reset-password/ResetPassword";
import ColleagueProfile from "./../components/content/colleague/ColleagueProfile";

const routes = [
  {
    path: "/",
    exact: true,
    name: "Layout",
    element: <Layout />,
    render: <Profile />,
  },
  {
    path: "/me",
    exact: true,
    name: "Profile",
    element: <Layout />,
    render: <Profile />,
  },
  {
    path: "/performance",
    exact: true,
    name: "Performance",
    element: <Layout />,
    render: <Performance />,
  },
  {
    path: "/colleagues",
    exact: true,
    name: "Colleagues",
    element: <Layout />,
    render: <Colleagues />,
  },
  {
    path: "/colleagues/profile/:userId",
    exact: true,
    name: "Colleague Profile",
    element: <Layout />,
    render: <ColleagueProfile />,
  },
  {
    path: "/colleagues/review",
    exact: true,
    name: "Review Colleague",
    element: <Layout />,
    render: <ColleagueReview />,
  },
  {
    path: "/colleagues/review/:colleagueId",
    exact: true,
    name: "Review Colleague",
    element: <Layout />,
    render: <ReviewDetails />,
  },
  {
    path: "/teams",
    exact: true,
    name: "Teams",
    element: <Layout />,
    render: <Teams />,
  },
  {
    path: "/team/members/:teamId",
    exact: true,
    name: "Team Members",
    element: <Layout />,
    render: <ViewTeamMembers />,
  },
  {
    path: "/team/member/profile/:userId",
    exact: true,
    name: "Team Member Profile",
    element: <Layout />,
    render: <ColleagueProfile />,
  },
  {
    path: "/registration",
    name: "Registration Page",
    element: <Registration />,
  },

  {
    path: "/login",
    name: "Login Page",
    element: <Login />,
  },

  {
    path: "/first-time-login",
    name: "First Time Login Page",
    element: <FirstTimeLogin />,
  },

  {
    path: "/new-password",
    name: "Set New Password",
    element: <ChangePassword />,
  },

  {
    path: "/set-new-password",
    name: "Set New Password",
    element: <FirstTimePasswordSetup />,
  },

  {
    path: "/forgot-password",
    name: "Forgot Password",
    element: <ForgotPassword />,
  },
  {
    path: "/reset-password",
    name: "Forgot Password",
    element: <ResetPassword />,
  },

  {
    path: "/home",
    name: "Home",
    element: <Layout />,
    render: <Profile />,
  },

  {
    path: "/dashboard",
    name: "Dashboard",
    element: <Layout />,
    render: <Profile />,
  },
];

export default routes;
