import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
//components
import { ProfileImage, ProfileCard } from "../";
import { Grid } from "@mui/material";
//services
import { getUser } from "../../../api/userService";
import "../../../css/profile.css";

const ColleagueProfile = () => {
  const { userId } = useParams();
  const [colleagueInfo, setColleagueInfo] = useState({});
  async function fetchApi() {
    try {
      const response = await getUser(userId);
      setColleagueInfo(response?.data);
    } catch (error) {
      console.log("error ", error);
    }
  }
  useEffect(() => {
    fetchApi();
    //eslint-disable-next-line
  }, []);
  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={8}>
          <ProfileCard page="ColleagueProfile" user={colleagueInfo} />
        </Grid>
        <Grid item xs={4}>
          <ProfileImage
            page="ColleagueProfile"
            user={colleagueInfo}
            width="8rem"
            height="8rem"
          />
        </Grid>
      </Grid>
    </>
  );
};

export default ColleagueProfile;
